// we import dmtJS and connectome stores only here !
// we pass dmtJS around in svelte context
import * as dmtJS from '../../../../dmt-js';
// do not import the whole connectome
// svelte will produce the error: Error: UMD and IIFE output formats are not supported for code-splitting builds.
// if we import only stores then tree shaking makes sure that dynamic imports in connectome contentServer are not a problem in build process
// for dynamic imports in general we have to use 'esm' build in rollup.config.js ... this is already supported in major browsers
// but was still a bit problematic for two reasons a) seemed to break css layout on immediate reload after compile b) unbounded growth of main-***.js files in public/build
import { stores, concurrency } from '../../../../connectome';

const { metamask } = dmtJS;

// using this pattern: https://stackoverflow.com/a/35443130/458177
// reason: we want to import connectome stores (and all dmt deps) in a single place
import loginStoreFactory from './loginStore';
const LoginStore = loginStoreFactory(stores.SimpleStore);

import connectedStoreFactory from './connectedStore';
const ConnectedStore = connectedStoreFactory(stores.ConnectedStore);

import appHelper from './appHelper'; // our custom helper object

appHelper.deps = { dmtJS };

const { LogStore } = stores;

const { metamaskInit } = metamask;

import App from './App.svelte';

const port = appHelper.ssl ? '/ws' : 7780; // hackish ?
const protocol = 'zeta';
const protocolLane = 'gui';

const logStore = new LogStore();

// source: https://stackoverflow.com/a/9216488
const log = console.log.bind(console);
console.log = (...args) => {
  logStore.addToLog({ origConsoleLog: log, limit: 100 }, ...args);
  log(...args);
};

const verbose = false;
const loginStore = new LoginStore();

const address = window.location.hostname;

const rpcRequestTimeout = 5500; // 500ms more than default so that if any underlying request time outs, we still get that info to the frontent (otherwise this request would time-out as well)
// todo: what if default changes ? we should somehow add 500ms to default... or specify hopNumber which decreases as we nest searches... current hop is multiplied by 500ms to allow for underlying timeouts
const store = new ConnectedStore(loginStore, {
  address,
  port,
  ssl: appHelper.ssl, // IT IS USED!!! not used yet.. use this only if we connect directly to our process and not via lighttpd /ws proxy
  protocol,
  protocolLane,
  rpcRequestTimeout,
  verbose,
  logStore
});

const metamaskConnect = metamaskInit(ethAddress => {
  // main place where we get always current connected account!
  // should be in sync with metamask state
  console.log(`Connected ethereum address: ${ethAddress}`);
  loginStore.login(ethAddress);
});

const app = new App({
  target: document.body,
  props: {
    store,
    concurrency,
    appHelper,
    metamaskConnect
  }
});

export default app;
